<template>
  <div>
    <md-progress-bar class="md-accent" md-mode="indeterminate" v-if="loaded"></md-progress-bar>
    <md-tabs class="md-primary" md-alignment="centered" @md-changed="tabs">
      <template slot="md-tab" slot-scope="{ tab }">
        {{ tab.label }} <i class="badge" v-if="tab.data.badge">{{ tab.data.badge }}</i>
      </template>
      <md-tab id="Active" md-label="Active">
      <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
            <md-table-toolbar>
              <div class="md-toolbar-section-start">
                <h1 class="md-title">Reserves</h1>
              </div>

              <md-field md-clearable class="md-toolbar-section-end">
                <md-input placeholder="Search by label..." v-model="search" @input="searchOnTable" />
              </md-field>
            </md-table-toolbar>

            <md-table-empty-state
              md-label="No reserves found"
              :md-description="`No reserves found for this '${search}' query. Try a different search term.`">
            </md-table-empty-state>

            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="ID" md-sort-by="_id" md-numeric>{{ item._id }}</md-table-cell>
              <md-table-cell md-label="Trailer ID" md-sort-by="trailerid">{{ item.trailerid }}</md-table-cell>
              <md-table-cell md-label="Truck ID" md-sort-by="truckid">{{ item.truckid }}</md-table-cell>
              <md-table-cell md-label="Reserved" md-sort-by="reserved">{{ item.reserved }}</md-table-cell>
              <md-table-cell md-label="Latitude" md-sort-by="lat">{{ item.lat }}</md-table-cell>
              <md-table-cell md-label="Longtitude" md-sort-by="long">{{ item.long }}</md-table-cell>
              <md-table-cell md-label="time" md-sort-by="time">{{ item.time }}</md-table-cell>
              <md-table-cell md-label="User" md-sort-by="user">{{ item.user }}</md-table-cell>
              <md-table-cell md-label="Status" md-sort-by="status">{{ item.status }}</md-table-cell>
            </md-table-row>
          </md-table>
      </md-tab>
      <md-tab id="Freezed" md-label="Freezed">
      <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
            <md-table-toolbar>
              <div class="md-toolbar-section-start">
                <h1 class="md-title">Reserves</h1>
              </div>

              <md-field md-clearable class="md-toolbar-section-end">
                <md-input placeholder="Search by label..." v-model="search" @input="searchOnTable" />
              </md-field>
            </md-table-toolbar>

            <md-table-empty-state
              md-label="No reserves found"
              :md-description="`No reserves found for this '${search}' query. Try a different search term.`">
            </md-table-empty-state>

            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="ID" md-sort-by="_id" md-numeric>{{ item._id }}</md-table-cell>
              <md-table-cell md-label="Trailer ID" md-sort-by="trailerid">{{ item.trailerid }}</md-table-cell>
              <md-table-cell md-label="Truck ID" md-sort-by="truckid">{{ item.truckid }}</md-table-cell>
              <md-table-cell md-label="Reserved" md-sort-by="reserved">{{ item.reserved }}</md-table-cell>
              <md-table-cell md-label="Latitude" md-sort-by="lat">{{ item.lat }}</md-table-cell>
              <md-table-cell md-label="Longtitude" md-sort-by="long">{{ item.long }}</md-table-cell>
              <md-table-cell md-label="time" md-sort-by="time">{{ item.time }}</md-table-cell>
              <md-table-cell md-label="User" md-sort-by="user">{{ item.user }}</md-table-cell>
              <md-table-cell md-label="Status" md-sort-by="status">{{ item.status }}</md-table-cell>
              <md-table-cell md-label="Actions"><md-menu md-size="medium" md-align-trigger>
                <md-button class="md-icon-button md-raised" md-menu-trigger @click="selectedTrailer = item.id">
                    <md-icon>edit</md-icon>
                </md-button>
                <md-menu-content>
                    <md-menu-item @click="statusMenu('Available', item._id, item.trailerid, item.truckid)">Available</md-menu-item>
                </md-menu-content>
                </md-menu>
            </md-table-cell>
            </md-table-row>
          </md-table>
      </md-tab>
    </md-tabs>
  </div>
</template>

<script>
import axios from 'axios'

const toLower = text => {
  return text.toString().toLowerCase()
}
const searchByName = (items, term) => {
  if (term) {
    return items.filter(item => toLower(item.first_name).includes(toLower(term)))
  }
  return items
}
export default {
  name: 'Reserves',
  data: () => ({
    url: 'http://ben-logistics-api.eu-4.evennode.com/graphql',
    loaded: true,
    search: null,
    searched: [],
    reserves: null
  }),
  methods: {
    searchOnTable () {
      this.searched = searchByName(this.reserves, this.search)
    },
    statusMenu (st, id, trailerid, truckid) {
      if (st === 'Available') {
        axios.post(this.url, {
          query: `mutation { freeReserve(_id:"` + id + `",trailerid:` + trailerid + `, truckid: ` + truckid + `){ _id } }`
        }).catch(e => {
          console.log(e)
        })
        this.fetchFreezedData()
      }
    },
    tabs (activeTab) {
      if (activeTab === 'Active') { this.loaded = true; this.fetchActiveData() }
      if (activeTab === 'Freezed') { this.loaded = true; this.fetchFreezedData() }
    },
    fetchActiveData () {
      axios.post(this.url, {
        query: `{ activeReserves { _id trailerid reserved lat long time truckid user status } }`
      }).then(res => {
        this.reserves = res.data.data.activeReserves
        this.searched = this.reserves
        this.loaded = false
      })
    },
    fetchFreezedData () {
      axios.post(this.url, {
        query: `{ freezedReserves { _id trailerid reserved lat long time truckid user status } }`
      }).then(res => {
        this.reserves = res.data.data.freezedReserves
        this.searched = this.reserves
        this.loaded = false
      })
    }
  },
  created () {
    this.fetchActiveData()
  }
}
</script>



