<template>
  <div>
    <md-progress-bar class="md-accent" md-mode="indeterminate" v-if="loaded"></md-progress-bar>
    <md-tabs md-sync-route class="md-accent" md-alignment="centered" @md-changed="tabs">
      <template slot="md-tab" slot-scope="{ tab }">
        {{ tab.label }} <i class="badge" v-if="tab.data.badge">{{ tab.data.badge }}</i>
      </template>
      <md-tab id="Unassigned" md-label="Unassigned" :md-template-data="{ badge: cnt.unassigned }">
        <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
          <md-table-row slot="md-table-row" slot-scope="{ item }">
            <md-table-cell md-label="ID" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
            <md-table-cell md-label="Vin" md-sort-by="vin">{{ item.vin }}</md-table-cell>
            <md-table-cell md-label="Label" md-sort-by="label">{{ item.label }}</md-table-cell>
            <md-table-cell md-label="Color" md-sort-by="color">{{ item.color }}</md-table-cell>
            <md-table-cell md-label="Make" md-sort-by="make">{{ item.make }}</md-table-cell>
            <md-table-cell md-label="Model" md-sort-by="model">{{ item.model }}</md-table-cell>
            <md-table-cell md-label="DeviceSerialNumber" md-sort-by="deviceSerialNumber">{{ item.deviceSerialNumber }}</md-table-cell>
            <md-table-cell md-label="Year" md-sort-by="year">{{ item.year }}</md-table-cell>
            <md-table-cell md-label="isAvailable" md-sort-by="isAvailable">{{ item.isAvailable }}</md-table-cell>
            <md-table-cell md-label="Actions"><md-menu md-size="medium" md-align-trigger>
                <md-button class="md-icon-button md-raised" md-menu-trigger @click="selectedTruck = item.id">
                    <md-icon>edit</md-icon>
                </md-button>
                <md-menu-content>
                    <md-menu-item @click="statusMenu('Available')">Available</md-menu-item>
                    <md-menu-item @click="statusMenu('Blocked')">Blocked</md-menu-item>
                </md-menu-content>
                </md-menu>
            </md-table-cell>
          </md-table-row>
        </md-table>
      </md-tab>

      <md-tab id="Reserved" md-label="Reserved" :md-template-data="{ badge: cnt.reserved }">
        <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
          <md-table-row slot="md-table-row" slot-scope="{ item }">
            <md-table-cell md-label="ID" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
            <md-table-cell md-label="Vin" md-sort-by="vin">{{ item.vin }}</md-table-cell>
            <md-table-cell md-label="Label" md-sort-by="label">{{ item.label }}</md-table-cell>
            <md-table-cell md-label="Color" md-sort-by="color">{{ item.color }}</md-table-cell>
            <md-table-cell md-label="Make" md-sort-by="make">{{ item.make }}</md-table-cell>
            <md-table-cell md-label="Model" md-sort-by="model">{{ item.model }}</md-table-cell>
            <md-table-cell md-label="DeviceSerialNumber" md-sort-by="deviceSerialNumber">{{ item.deviceSerialNumber }}</md-table-cell>
            <md-table-cell md-label="Year" md-sort-by="year">{{ item.year }}</md-table-cell>
            <md-table-cell md-label="isAvailable" md-sort-by="isAvailable">{{ item.isAvailable }}</md-table-cell>
          </md-table-row>
        </md-table>
      </md-tab>

      <md-tab id="Available" md-label="Available" :md-template-data="{ badge: cnt.available }">
        <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
          <md-table-row slot="md-table-row" slot-scope="{ item }">
            <md-table-cell md-label="ID" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
            <md-table-cell md-label="Vin" md-sort-by="vin">{{ item.vin }}</md-table-cell>
            <md-table-cell md-label="Label" md-sort-by="label">{{ item.label }}</md-table-cell>
            <md-table-cell md-label="Color" md-sort-by="color">{{ item.color }}</md-table-cell>
            <md-table-cell md-label="Make" md-sort-by="make">{{ item.make }}</md-table-cell>
            <md-table-cell md-label="Model" md-sort-by="model">{{ item.model }}</md-table-cell>
            <md-table-cell md-label="DeviceSerialNumber" md-sort-by="deviceSerialNumber">{{ item.deviceSerialNumber }}</md-table-cell>
            <md-table-cell md-label="Year" md-sort-by="year">{{ item.year }}</md-table-cell>
            <md-table-cell md-label="isAvailable" md-sort-by="isAvailable">{{ item.isAvailable }}</md-table-cell>
          </md-table-row>
        </md-table>
      </md-tab>

      <md-tab id="Blocked" md-label="Blocked" :md-template-data="{ badge: cnt.blocked }">
        <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
          <md-table-row slot="md-table-row" slot-scope="{ item }">
            <md-table-cell md-label="ID" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
            <md-table-cell md-label="Vin" md-sort-by="vin">{{ item.vin }}</md-table-cell>
            <md-table-cell md-label="Label" md-sort-by="label">{{ item.label }}</md-table-cell>
            <md-table-cell md-label="Color" md-sort-by="color">{{ item.color }}</md-table-cell>
            <md-table-cell md-label="Make" md-sort-by="make">{{ item.make }}</md-table-cell>
            <md-table-cell md-label="Model" md-sort-by="model">{{ item.model }}</md-table-cell>
            <md-table-cell md-label="DeviceSerialNumber" md-sort-by="deviceSerialNumber">{{ item.deviceSerialNumber }}</md-table-cell>
            <md-table-cell md-label="Year" md-sort-by="year">{{ item.year }}</md-table-cell>
            <md-table-cell md-label="isAvailable" md-sort-by="isAvailable">{{ item.isAvailable }}</md-table-cell>
          </md-table-row>
        </md-table>
      </md-tab>

      <md-tab id="All" md-label="All" :md-template-data="{ badge: cnt.all }">
        <md-table v-model="searched" md-sort="name" md-sort-order="asc" md-card md-fixed-header>
          <md-table-row slot="md-table-row" slot-scope="{ item }">
            <md-table-cell md-label="ID" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
            <md-table-cell md-label="Vin" md-sort-by="vin">{{ item.vin }}</md-table-cell>
            <md-table-cell md-label="Label" md-sort-by="label">{{ item.label }}</md-table-cell>
            <md-table-cell md-label="Color" md-sort-by="color">{{ item.color }}</md-table-cell>
            <md-table-cell md-label="Make" md-sort-by="make">{{ item.make }}</md-table-cell>
            <md-table-cell md-label="Model" md-sort-by="model">{{ item.model }}</md-table-cell>
            <md-table-cell md-label="DeviceSerialNumber" md-sort-by="deviceSerialNumber">{{ item.deviceSerialNumber }}</md-table-cell>
            <md-table-cell md-label="Year" md-sort-by="year">{{ item.year }}</md-table-cell>
            <md-table-cell md-label="isAvailable" md-sort-by="isAvailable">{{ item.isAvailable }}</md-table-cell>
          </md-table-row>
        </md-table>
      </md-tab>
    </md-tabs>
  </div>
</template>

<script>
import axios from 'axios'

const toLower = text => {
  return text.toString().toLowerCase()
}
const searchByName = (items, term) => {
  if (term) {
    return items.filter(item => toLower(item.label).includes(toLower(term)))
  }
  return items
}
export default {
  name: 'Trucks',
  data: () => ({
    url: 'http://ben-logistics-api.eu-4.evennode.com/graphql',
    loaded: true,
    search: null,
    searched: [],
    trucks: null,
    cnt: null,
    selectedTruck: null
  }),
  methods: {
    tabs (activeTab) {
      if (activeTab === 'Unassigned') { this.loaded = true; this.countStatus(); this.fetchUnassignedData() }
      if (activeTab === 'Reserved') { this.loaded = true; this.countStatus(); this.fetchReservedData() }
      if (activeTab === 'Available') { this.loaded = true; this.countStatus(); this.fetchAvailableData() }
      if (activeTab === 'Blocked') { this.loaded = true; this.countStatus(); this.fetchBlockedData() }
      if (activeTab === 'All') { this.loaded = true; this.countStatus(); this.fetchData() }
    },
    statusMenu (st) {
      var state
      if (st === 'Available') state = 'true'
      if (st === 'Blocked') state = 'blocked'
      axios.post(this.url, {
        query: `mutation{ setTruckStatus (id:` + this.selectedTruck + `,status: "` + state + `"){ id }}`
      })
    },
    searchOnTable () {
      this.searched = searchByName(this.trucks, this.search)
    },
    reshreshData () {
      this.loaded = true
      this.trucks = null
      this.fetchData()
    },
    fetchBlockedData () {
      axios.post(this.url, {
        query: `{blockedTrucks{id vin label color make model deviceSerialNumber year isAvailable } }`
      }).then(res => {
        this.trucks = res.data.data.blockedTrucks
        this.searched = this.trucks
        this.loaded = false
      })
    },
    fetchAvailableData () {
      axios.post(this.url, {
        query: `{availableTrucks{id vin label color make model deviceSerialNumber year isAvailable } }`
      }).then(res => {
        this.trucks = res.data.data.availableTrucks
        this.searched = this.trucks
        this.loaded = false
      })
    },
    fetchReservedData () {
      axios.post(this.url, {
        query: `{reservedTrucks{id vin label color make model deviceSerialNumber year isAvailable } }`
      }).then(res => {
        this.trucks = res.data.data.reservedTrucks
        this.searched = this.trucks
        this.loaded = false
      })
    },
    fetchUnassignedData () {
      axios.post(this.url, {
        query: `{unassignedTrucks{id vin label color make model deviceSerialNumber year isAvailable } }`
      }).then(res => {
        this.trucks = res.data.data.unassignedTrucks
        this.searched = this.trucks
        this.loaded = false
      })
    },
    fetchData () {
      axios.post(this.url, {
        query: `{trucks{id vin label color make model deviceSerialNumber year isAvailable } }`
      }).then(res => {
        this.trucks = res.data.data.trucks
        this.searched = this.trucks
        this.loaded = false
      })
    },
    countStatus () {
      axios.post(this.url, {
        query: `{ countTruck{ all unassigned reserved available blocked }}`
      }).then(res => {
        this.cnt = res.data.data.countTruck
      })
    }
  },
  created () {
    this.countStatus()
    this.fetchUnassignedData()
  }
}
</script>

<style lang="scss" scoped>
  .badge {
    width: 19px;
    height: 19px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 2px;
    right: 2px;
    background: red;
    border-radius: 100%;
    color: #fff;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: -.05em;
    font-family: 'Roboto Mono', monospace;
  }
</style>



